#navbar {
  height: 50px;
  background-color: white;
  border: 2px solid black;
  display: flex;
  justify-content: space-around;

  .link {
    text-decoration: none;
    color: black;
    font-family: $marker;
    font-size: 30px;
    width: 200px;

  }

  .link:hover {
    color: $aqua;
    font-size: 35px;

  }

}