#about-container {
  color: white;
  max-width: 1400px;
  margin: 0 auto;


  // #about-title {
  //   // margin-bottom: 20px !important;
  //   font-size: 25px;

  // }

  .about-content {
    display: flex;
    max-width: inherit;
    width: inherit;

    .about-div {
      padding: 20px;
      margin: 10px;
      border: 2px solid $aqua;
      width: calc((100% /3) - 10px);

      .about-heading {
        font-size: 35px;
        // padding: 10px;
        white-space: nowrap;
        color: $aqua !important;
      }
      #se-text {
        background-color: rgba($color: white, $alpha: .90);
      }

      .about-top {
        border: 2px solid $pink;
        background-color: white;
        position: relative;
        width: 100%;
        height: 90%;
        overflow: hidden;

        .about-text{
          width: 100%;
          p {
            font-size: 40px;
            color: $blue;
          }
        }

      }

    }

  }
  .about-link {
    margin: 20px auto;
    text-decoration: none;
    font-family: $body;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 30px;
    border-radius: 6px;
    padding: 10px 60px;
    background:
      linear-gradient($pink, transparent),
      linear-gradient(to top left, $aqua, transparent),
      linear-gradient(to top right, $blue, transparent);
    background-blend-mode: screen;
    width: 100%;
  }
  .about-link:hover {
    transform: scale(1.2);
    font-size: 25px;
    cursor: url('../assets/lets-go2.png'), pointer;
  }

  .about-text {
    text-align: justify !important;

  }

  .img-overlay {
    width: 100%;
    height: 100%;
    transition: all 1s ease-in-out;
    z-index: 100;
    position: absolute;
    background-size: cover;
  }

  #se-img {
    background-image: url('../assets/Excalidraw.png');
    background-position:bottom ;

  }
  #neuro-img {
    background-image: url('../assets/brain1.JPG');
    background-position: center;

  }
  #snowboarder-img {
    background-image: url('../assets/boardingCloseup.png');

  }

  .about-top:hover {
    .img-overlay {
      transform: translateY(-100%);
    }

  }


}